/*
This is the contacts component 
renders the details of the person who has logged in
*/



import React, { useState } from 'react';
import Nav from './Nav'
import ErrorMessage from './Error'
import Footer from './Footer'
import { Link } from "react-router-dom";


function Contacts(props) {
    /*
    Used React Hooks to hold and update data
    */

    const [checked, setchecked] = useState(false);
    const [sendCode, setSendCode] = useState();
    const [passcode, setPasscode] = useState(false);

        /*
           Quick check to see if the user location has been correctly updated. i.e user logged in with
           the correct user credentials then the search array is populated with that user 
        */

    let user = props.location.state === undefined ? [] : props.location.state.search

       /*
         This handler takes the innerText of the onclick binding and renders the code div for the user to enter the unique code
         This is only visible for 1 min with what the user has clicked i.e mobile/phone/email
        */
    const handelClick = (e) => {
        setchecked(true)
        setSendCode(e.target.children[0].innerText)
        setTimeout(() => { setchecked(false) }, 60000);
    }

    return (
        <div>
            <Nav />
            {
                props.match.isExact === true && props.location.state === undefined ?
                    <ErrorMessage />

                    :
                    <main role="main" className="container" >
                        <div className="text-center">
                            <h3 className="mb-5 mx-auto ">Hospital Patient Portal</h3>
                        </div>
                        <div className="my-3 p-3 bg-white rounded shadow-sm">
                            <h6 className="border-bottom border-gray pb-2 mb-0">Suggestions</h6>
                            <div className="media text-muted pt-3">
                                {/* <svg className="bd-placeholder-img mr-2 rounded" width="32" height="32" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: 32x32"><title>Placeholder</title><rect width="100%" height="100%" fill="#007bff" /><text x="50%" y="50%" fill="#007bff" dy=".3em">32x32</text></svg> */}
                                <i className="fas fa-user"></i>
                                <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                                    <div className="d-flex justify-content-between align-items-center w-100">
                                        <strong className="text-gray-dark">{user.fullName} : Please click on the any contact details below to receive the code. </strong>
                                    </div>
                                    <br />
                                    {/* handler binding to onClick Handler   */}
                                    <span onClick={(e) => handelClick(e)} style={{ cursor: "pointer" }} className={`d-block`}><strong>Mobile: </strong> {user.mobileNumber}</span>
                                    <span onClick={(e) => handelClick(e)} style={{ cursor: "pointer" }} className="d-block"><strong>Landline: </strong> {user.landlineNumber}</span>
                                    <span onClick={(e) => handelClick(e)} style={{ cursor: "pointer" }} className="d-block"><strong>Email: </strong> {user.email}</span>
                                </div>
                            </div>

                            <small className="d-block text-right mt-3">
                                <Link to={'/'}><i className="fas fa-home"></i></Link>
                            </small>
                        </div>
                        {
                            checked !== false ?
                                <div className="container text-center">
                                    <h3>CODE</h3>
                                    <small className="d-block">4 Digit Code has been sent to your {sendCode} <br /> Please enter below </small><br />

                                    {/* check password, this has been hardcoded to 0000 if its correct the passcode block will appear for user to go to details page */}
                                    <input pattern='[0-9]{0,5}' onChange={(e) => { return e.target.value === '0000' ? setPasscode(true) : setPasscode(false) }} maxLength="4" width="100" type="text" placeholder="" aria-label="" aria-describedby="basic-addon1" />
                                    <small className={`text-center mt-3 ${passcode ? "d-block" : "d-none"}`}>
                                        <Link to={{ pathname: '/welcome/' + passcode, state: { user } }}><i className="fas fa-sign-in-alt"></i><br />Enter</Link>

                                    </small>
                                </div>
                                :
                                <React.Fragment />
                        }

                    </main>

            }
            <Footer />
        </div>
    );
}

export default Contacts;