/*
Welcome page once the user has entered the code their Personal Details will appear here of everything that was added in the JSON.

*/




import React from 'react';
import Nav from './Nav'
import Footer from './Footer'
import ErrorMessage from './Error'
import { Link } from "react-router-dom";

function Welcome(props) {
    
    let user = props.location.state === undefined ? [] : props.location.state.user

    return (
        <div>
            <Nav />
            {
                // function to handle state if the array is empty/undefined or isExact error meesage will render else Welcome user
                props.match.isExact === true && props.location.state === undefined ?
                    <ErrorMessage />
                    :
                    <main role="main" className="container" >
                        <div className="container text-center">
                            <h3 className="mb-5 mx-auto ">Hospital Patient Portal Dashboard</h3>
                        </div>

                        <div className="my-3 p-3 bg-white rounded shadow-sm">
                            <h6 className="border-bottom border-gray pb-2 mb-0">Welcome {user.fullName}</h6>
                            <div className="media text-muted pt-3">
                            <i className="fas fa-user"></i>
                                <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                                    <div className="d-flex justify-content-between align-items-center w-100">
                                        <strong className="text-gray-dark"> Personal Details</strong>

                                    </div>
                                    <ul>
                                        <li><strong>Full Name:</strong> {user.fullName}</li>
                                        <li><strong>Date of Birth:</strong>{user.dob}</li>
                                        <li><strong>Post Code:</strong>{user.postcode}</li>
                                        <li><strong>Mobile Number:</strong>{user.mobileNumber}</li>
                                        <li><strong>Landline Number:</strong>{user.landlineNumber}</li>
                                        <li><strong>Email:</strong>{user.email}</li>
                                    </ul>
                                </div>
                            </div>

                            <small className="d-block text-right mt-3">
                            <Link  to={'/'}><i className="fas fa-home"></i></Link>
                            </small>



                        </div>


                    </main>


            }
            <Footer />
        </div>
    );

}

export default Welcome;