/*
Login component contains ALL the Login page logic 
*/



import React, { useState } from 'react';
import drLogo from '../img/drdoctor-company-logo.png'
import { Link } from "react-router-dom";


function Login(props) {

    // React hooks to hold data/ state updates
    const [login, setLogin] = useState(false);
    const [lastName, setLastName] = useState('')
    const [dob, setDob] = useState('')
    const [errorLastName, setErrorLastName] = useState(false)
    const [error, setErrorDob] = useState(false)
    const [errorPost, setErrorPost] = useState(false)
    const [postcode, setPostcode] = useState('')
    const [search, setSearchUser] = useState([]);

    let { regUsers } = props
    //get information from fields and check Array for User, if found update set Login state

    let checkArray = () => {
        var FormElement = document.querySelector('.form-signin')
        if (FormElement.checkValidity() === true && FormElement.reportValidity() === true) {
           return regUsers.map((user) => {
                if (user.lastname === lastName && user.dob === dob && user.postcode === postcode) {
                    setSearchUser(user)
                    setLogin(true)
                }
            }
            )
        }
    }

    //last name uses a regex pattern to make sure that the field only contains letters not numbers
    //then the state is updated 
    const lastNameCheck = (e) => {
        const lastNameCheckPattern = /^[A-Za-z]+$/ig
        if (lastNameCheckPattern.test(e)) {
            setLastName(e)
            setErrorLastName(false)
        } else {
            setErrorLastName(true)
        }
    }
    //DOB check uses a regex pattern to make sure that its a vaild dob in the format [DD]/[MM]/[YYYY]
    //then the state is updated 
    const dobCheck = (e) => {
        const dobPattern = /^\s*(3[01]|[12][0-9]|0?[1-9])\/(1[012]|0?[1-9])\/((?:19|20)\d{2})\s*$/g
        if (dobPattern.test(e)) {
            setDob(e)
            setErrorDob(false)
        } else {
            setErrorDob(true)
        }
    }
   //postCode check uses a regex pattern to make sure that its a vaild Postcode format 
    //then the state is updated 
    const postCodeCheck = (e) => {
        const postcodePattern = /^[a-z]{1,2}\d[a-z\d]?\s*\d[a-z]{2}$/i
        if (postcodePattern.test(e)) {
            setPostcode(e)
            setErrorPost(false)
        } else {
            setErrorPost(true)
        }
    }
    
    return (
          // render JSX/HTML
        <form className="form-signin">
            <div className="text-center mb-4">
                <img className="mb-4 img-fluid " src={`${drLogo}`} alt="" width="250" height="65" />
                <h1 className="h3 mb-3 font-weight-normal">Hospital Patient Portal</h1>
                <p>Please enter the following information</p>
            </div>

            <div className="form-label-group">
                <input type="text" maxLength={50} id="inputPatientlastname" onChange={(e) => { lastNameCheck(e.target.value.toLowerCase()) }} className="form-control" placeholder="Patient lastname" required autoFocus />
                <label htmlFor="Patientlastname">Last Name</label>
                <small className={`text-danger ${errorLastName? "b-block" : "d-none"} `} role="alert">Please enter vaild last name</small>

            </div>

            <div className="form-label-group">
                <input type="text" maxLength={10} className="form-control" onKeyUp={(e) => { dobCheck(e.target.value) }} placeholder="Patient Date Of Birth" required />
                <label htmlFor="inputdob">Date Of Birth</label>
                <small className={`text-danger ${error ? "b-block" : "d-none"} `} role="alert">Please enter valid Date of Birth DD/MM/YYYY</small>
            </div>

            <div className="form-label-group">
                <input type="text" id="inputpostcode" className="form-control" onChange={(e) => { postCodeCheck(e.target.value.toUpperCase()) }} placeholder="Patient Post Code" required />
                <label htmlFor="inputpostcode">Post Code</label>
                <small className={`text-danger ${errorPost ? "b-block" : "d-none"} `} role="alert">Please enter valid Post Code example: W5 3JT</small>

            </div>


            <div className="checkbox mb-3">
                <label><input type="checkbox" value="remember-me" /> Remember me</label>
            </div>


            <Link className="btn btn-lg btn-dark  btn-block" innerRef={(e) => checkArray(e)} to={{ pathname: '/contacts/' + login, state: { search } }}>
                Submit
            </Link>
            <div className="text-center mt-5">
                <small className="mt-5 mb-3 text-muted text-center">Dr Doctor&copy; 2020 All Rights Reserved.</small>
            </div>
        </form>

    );
}

export default Login;