/*
This is the APP Componenet all other Componenets are imported and rendered here including CSS files.
The Data is imported from users.json feel free to add another person to the object array and they will be
added to the system.

I have used HasRouter from React Router for custom routes and to create an SPA 
is there is no data or a route that doesnt exist it will re-route to 404
*/


import React from 'react';
import '../css/App.css';
import Login from './Login'
import Contacts from './Contacts'
import Welcome from './Welcome'
import NotFound from './NotFound'
import Data from './users.json'

import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


function App() {
  const users = Data;
  
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/" component={() => <Login regUsers={users} />} />
          <Route exact path="/contacts/true" component={Contacts} />
          <Route exact path="/welcome/true" component={Welcome} />
          <Route component={NotFound} />
        </Switch>
      </Router>
    </div>

  );
}

export default App;
